/* general */

body {
  font-family: "Nunito", Semi-bold;
  background-color: #ffffff !important;
  max-width: 100%;
}

h1 {
  font-size: 2.5em;
}

/* Partie 1 */

.partie1 {
  background-image: url("./fond.png");
}
.colUserMobile {
  display: none;
}

.logoPartie1 {
  text-align: center;
  padding-bottom: 40%;
  padding-top: 2%;
}

.logoPartie1 img {
  width: 25%;
}

.colButton {
  text-align: right;
  margin-top: 0.5%;
}
.btn {
  background-color: transparent !important;
  border-color: white;
  border-radius: 28px;
}

/* partie 2 services titres */

.partie2 {
  padding-top: 20%;
  z-index: 2;
}

.partie2Titre {
  text-align: center;
  color: #f5a624;
}

.partie2SousTitre {
  text-align: center;
}

/*mockups*/
.mockup1 {
  position: absolute;
  text-align: center;
  top: 35%;
  left: -11%;
  z-index: 1;
}
.mockup2 {
  position: absolute;
  text-align: center;
  top: 49%;
  left: 9%;
  z-index: 3;
}
/* partie 3 services blocs */

.partie3 {
  padding-top: 10%;
}

.partie3 p {
  padding-left: 17%;
  padding-right: 17%;
}
.partie3Titre {
  text-align: center;
  color: #f5a624;
}

.clientBloc {
  text-align: center;
}

.serveurBloc {
  text-align: center;
}

.restaurateurBloc {
  text-align: center;
}

.clientBloc img {
  width: 12%;
  margin-bottom: 6%;
}

.serveurBloc img {
  width: 24%;
  margin-bottom: 7%;
}

.restaurateurBloc img {
  width: 22%;
  margin-bottom: 5%;
}

/* partie 4 environnement*/

.partie4 {
  padding-top: 10%;
}

.partie4Titre {
  text-align: center;
  color: #f5a624;
}

.partie4 img {
  width: 14%;
  margin-top: 10%;
}
.partie4SousTitre {
  text-align: center;
}

/*partie 5 contact */

.partie5 {
  background-color: #f5a624;
}

.partie5 img {
  width: 26%;
}

.partie5image {
  text-align: center;
  padding-top: 4%;
}

.partie5Contact {
  text-align: center;
  color: #ffffff;
  padding-top: 4%;
}

.adresseContact {
  text-decoration: underline #c4c4c4;
}

.fab {
  color: #ffffff;
  margin: 12px;
  font-size: 37px;
  margin-right: 10px;
}

.iconeFab {
  text-align: center;
}

.mockup1mobile {
  display: none;
}

.mockup1Ipad {
  display: none;
}

.mockup2Ipad {
  display: none;
}
@media (max-width: 640px) {
  /* CSS mobile ici */

  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }

  .logoPartie1 img {
    width: 63%;
    margin-top: 20%;
    padding-bottom: 0%;
  }
  .mockup1mobile {
    display: block !important;
  }

  .mockup1mobile img {
    width: 83%;
  }
  .colButton {
    display: none;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }

  .mockup1Ipad {
    display: block;
    position: absolute;
    top: 13%;

    left: 19%;
  }

  .mockup1Ipad img {
    width: 119%;
  }

  .mockup2Ipad {
    display: block;
    position: absolute;
    top: 15%;
    left: 42%;
  }

  .mockup2Ipad img {
    width: 109%;
  }

  .colButton {
    display: none;
  }
}
